.app.dark {
  background-color: #111;
  //   color: rgb($color: #fff, $alpha: 0.9);
  color: rgb(156, 156, 156);

  .chartGrid {
    stroke: rgba(228, 228, 228, 0.219);
  }

  .table {
    background-color: #111;
    .tableCell {
      color: gray;
    }
  }

  .navbar {
    color: #999;
    border-color: #333;
    .search {
      border-color: gray;
    }
  }

  .sidebar {
    background-color: #111;
    border-color: #333;

    .top {
      .logo {
        color: #999;
      }
    }
    hr {
      border-color: #333;
    }

    ul {
      li {
        &:hover {
          background-color: #333;
        }

        .icon {
          color: #999;
        }
      }
    }
  }

  .datatable {
    .datagrid {
      color: gray;
      border: none;

      .viewButton,
      .deleteButton,
      .cellWithStatus {
        color: gray;
        border: none;
      }

      .viewButton,
      .deleteButton {
        text-decoration: none;
        font-size: 16px;
        font-weight: 400;
        padding: 5px;
        border-radius: 5px;
        cursor: pointer;
        transition: all 0.3s;

        &:hover {
          color: #fff;
        }
      }

      .viewButton {
        border: 1px solid #144a14;
        color: green;
        &:hover {
          background-color: #144a14;
        }
      }
      .deleteButton {
        border: 1px solid #870808;
        color: red;
        &:hover {
          background-color: #870808;
        }
      }
    }
  }

  input {
    background-color: transparent;
    color: #fff;
  }
}
